.footer {
  color: #777;
  padding: 10px 15px;
  height: 20px;
  text-align: center;
  border-top: 1px solid #e6e6e6;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &::before {
    content: '';
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    height: 50px;
    overflow: hidden;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
  }

  & .itemCount {
    font-size: 14px;
    font-weight: 200;
  }

  & .filterBtn,
  & .clearCompleted {
    font-size: 14px;
    font-weight: 200;
    border: 1px solid transparent;
    padding: 5px 10px;
    visibility: visible;
    transition: border-color 0.2s ease-in-out;
    cursor: pointer;
    outline: none;
  }

  & .filterBtn.border {
    border-color: rgba(175, 47, 47, 0.2);
    border-radius: 3px;
  }

  & .clearCompleted.hide {
    visibility: hidden;
  }

  & .filters {
    position: relative;
  }
}
